	module.exports = {"data":{"markdownRemark":{"html":"<h2>踏み逃げ</h2>\n<p>基本的にキリのいいアクセスカウントを取っても掲示板やメールなどで報告することなく立ち去ってしまう行為、あるいはその行為をする者を指します。</p>\n<h2>踏み逃げ禁止</h2>\n<p>原則としてキリのいい、あるいは管理人の指定したアクセスカウント番号を取った場合掲示板やメールなどで報告することを義務付けることを指します。</p>\n<h2>素通り禁止</h2>\n<p>上記をさらに凶悪化させたようなもので毎回対象のサイトにアクセスする度に掲示板やメールなどでの報告を義務付けられることを指します。</p>","frontmatter":{"cover":null,"date":"2002-08-08T05:57:10+09:00","strDate":"2002/8/7","title":"関連用語"}}},"pathContext":{"path":"/ja/blog/2002/08/08-関連用語/","langKey":"ja"}}

